@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: hsl(207, 26%, 17%);
  --front-color: hsl(0, 0%, 100%);
  --elements-color: hsl(209, 23%, 22%);
  --detail-color: hsl(0, 0%, 80%);

  --padding: 15%;
  --search-padding: 1rem;
  --border-radius: 5px;
  --box-shadow: hsla(0, 0%, 0%, 0.2) 0px 2px 8px 0px;
}

.Light, .Light * {
  --bg-color: hsl(0, 0%, 98%);
  --front-color: hsl(200, 15%, 8%);
  --elements-color: hsl(0, 0%, 100%);
  --detail-color: hsl(0, 0%, 20%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  background-color: hsl(207, 26%, 17%);
  background-color: var(--bg-color);
}

@media (min-width: 900px){
  :root {
    --padding: 5%;
  }
}
.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1.1rem var(--padding);
    
    color: var(--front-color);
    background-color: var(--elements-color);
    box-shadow: var(--box-shadow);
}

.header-left {
    font-size: 1rem;
    font-weight: 800;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.header-left:hover {
    cursor: pointer;
}

.header-right {
    display: flex;
    align-items: center;
    width: auto;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.header-right:hover {
    cursor: pointer;
}

.mode-icon{
    width: 0.8rem;
    height: 0.8rem;
}

.mode-text {
    font-size: 0.8rem;
    white-space: nowrap;
    font-weight: 600;
    margin-left: 0.3rem;
}

@media (min-width: 900px){
    .header-left {
        font-size: 1.4rem;
    }

    .mode-icon{
        width: 1rem;
        height: 1rem;
    }

    .mode-text {
        font-size: 1rem;
        margin-left: 0.5rem;
    }
}
.body-container {
    padding: 1rem var(--padding);
}

@media (min-width: 900px){
    .body-container {
        padding: 3rem var(--padding);
    }
}
.searchSection-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 3rem;
    font-size: 0.9rem;
}

.searchSection-container > *:not(:first-child){
    margin-top: 1.5rem;
}

@media (min-width: 900px){
    .searchSection-container {
        flex-direction: row;
    }

    .searchSection-container > *:not(:first-child){
        margin-top: 0;
    }
}
.search-container {
    display: flex;
    background-color: var(--elements-color);
    color: var(--front-color);
    padding: 0 var(--search-padding);
    border-radius: var(--border-radius);
    min-width: 40%;
    box-shadow: var(--box-shadow);
}

.search-container > * {
    padding: var(--search-padding);
}

.search-icon {
    width: 18px;
    height: auto;
    box-sizing: content-box;
}

.search-input {
    width: 100%;
    border: none;
    background-color: inherit;
    font-size: inherit;
    color: var(--front-color);
}

.search-input:focus {
    outline: none;
}

::-webkit-input-placeholder {
    color: var(--front-color);
}

:-ms-input-placeholder {
    color: var(--front-color);
}

::placeholder {
    color: var(--front-color);
}
.filter-container {
    position: relative;
    color: var(--front-color);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-shadow: var(--box-shadow);
    border-radius: var(--border-radius);
}

.selection{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--elements-color);
    padding: var(--search-padding);
    padding-left: calc(var(--search-padding) * 1.4);
    width: 200px;
    border-radius: var(--border-radius);
}

.selection:hover {
    cursor: pointer;
}

.arrow-icon {
    width: 12px;
    height: auto;
}

.filter-options {
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
    margin-top: 4px;
    padding: var(--search-padding);
    padding-left: calc(var(--search-padding) * 1.4);
    background-color: var(--elements-color);
    border-radius: var(--border-radius);
    box-shadow: var(--box-shadow);
}

.filter-options > div:not(:last-child) {
    padding-bottom: 0.5rem;
}

.filter-options > div:hover {
    cursor: pointer;
}
.resultSection-countainer {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 50px;
    gap: 50px;
}

@media (min-width: 900px){
    .resultSection-countainer {
        grid-template-columns: repeat(4, minmax(0, 1fr));
    }
}
.country-container {
    width: auto;
    background-color: var(--elements-color);
    border-radius: var(--border-radius);
    overflow: hidden;
    box-shadow: var(--box-shadow);
}

.country-container:hover {
    cursor: pointer;
}

.flag {
    width: 100%;
    height: 180px;
}

.country-infos {
    margin: calc(var(--search-padding) * 1.3);
    color: var(--front-color);
}

.country-infos > *:not(:first-child) {
    margin-bottom: 0.3rem;
}

.country-infos > *:not(:first-child) > span {
    color: var(--detail-color);
}

.name {
    font-size: 1.2rem;
    font-weight: 800;
    margin-bottom: 1.4rem;
}

@media (min-width: 900px){
    .flag {
        width: 100%;
        height: 150px;
    }
}
.detail-container {
    color: var(--front-color);
    display: flex;
    flex-direction: column;
    padding: var(--padding);
    padding-top: calc(var(--padding) / 2);
}

.back {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: var(--elements-color);
    border-radius: var(--border-radius);
    padding: 0.5rem 1.8rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-shadow: var(--box-shadow);
}

.back:hover {
    cursor: pointer;
}

.back-icon {
    width: 1rem;
    height: auto;
    margin-right: 1rem;
}

@media (min-width: 900px){
    
}
.details-container {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    margin-top: 4rem;
}

.details-container > * {
    flex: 1 1;
}

.detail-flag {
    width: 100%;
    height: auto;
}

.info-container {
    flex-grow: 1;
    padding: 2rem;
    color: var(--front-color);
}

.info-container > *:first-child {
    font-size: 1.7rem;
    font-weight: 800;
    margin-bottom: 1.5rem;
}

.details {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    font-weight: 600;
}

.details > :not(:first-child) {
    margin-top: 1.7rem;
}

.details > div > * {
    margin-bottom: 0.3rem;
}

.details span, .no-borders {
    color: var(--detail-color);
    font-weight: 300;
}

.border-countries-container {
    display: flex;
    align-items: center;
    margin-top: 1.8rem;
    flex-wrap: wrap;
    font-weight: 600;
}

.border-contry {
    border: none;
    background-color: var(--elements-color);
    border-radius: var(--border-radius);
    color: var(--front-color);
    padding: 0.4rem 1.5rem;
    margin: 0 5px;
    box-shadow: var(--box-shadow);
}

.border-contry:hover {
    cursor: pointer;
}

.border-countries-container > * {
    margin-bottom: 1rem;
}

@media (min-width: 900px){
    .details-container {
        flex-direction: row;
    }

    .detail-flag {
        width: 500px;
        height: 350px;
    }

    .details {
        flex-direction: row;
    }

    .details > :not(:first-child) {
        margin-top: 0;
    }

    .border-countries {
        margin-top: 4rem;
    }
}
